:root {
  --clr-primary: #651fff;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
}
.top-nav {
  background-color: #ffff;
  position: relative;
  width: 100%;
  height: 80px;
  box-shadow: 0 0.5rem 0.5rem rgba(0, 0, 0, 0.06);
}
.nav-container {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: flex-start;
  max-width: 1200px;
  margin: 0 auto;
  padding-right: 1rem;
  font-weight: bold;
}

.navigation-list {
  position: relative;
  list-style-type: none;
  float: left;
  margin-bottom: 8px;
  margin-left: 60px;
}
.nav-item {
  display: inline;
}
.logo {
  padding-left: 1rem;
  padding-top: 1.3rem;
}
.nav-link,
.logo a {
  padding: 0 0.2rem;
  color: #232b76;
  text-decoration: none;
  font-size: 14px;
  text-transform: uppercase;
}

.nav-link,
.logo img {
  padding: 0 1rem;
  color: #232b76;
  text-decoration: none;
}
.nav-link:hover,
.logo a:hover {
  color: #70a7d7;
}
.mobile-btn {
  display: none;
}

@media (max-width: 991.98px) {
  .nav-container {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
    padding-right: 1rem;
  }
  .navigation {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: black;
    right: 0;
    width: 0;
    overflow-x: hidden;
    transition: 0.3s;
    text-align: center;
  }
  .nav-active {
    width: 100%;
  }

  .navigation-list {
    width: 100%;
  }
  .mobile-btn {
    display: block;
    cursor: pointer;
    z-index: 1000;
    padding-right: 1rem;
  }
  .line1,
  .line2,
  .line3 {
    width: 24px;
    height: 3px;
    background-color: black;
    margin: 5px;
    transition: 0.3s;
  }
  /*.btn-active .line1 {
    background-color: white;
    -webkit-transform: rotate(-45deg) translate(-5px, 6px);
    transform: rotate(-45deg) translate(-5px, 6px);
  }
  .btn-active .line2 {
    opacity: 0;
  }
  .btn-active .line3 {
    background-color: white;
    -webkit-transform: rotate(45deg) translate(-5px, -6px);
    transform: rotate(45deg) translate(-5px, -6px);
  }*/
  .nav-item {
    display: block;
    width: 100%;
    padding: 1rem 0;
  }

  .nav-link {
    display: block;
    width: 100%;
    padding: 0.5rem 0;
    color: white;
  }
}

#footer {
  background-color: #ffff;
  padding: 0.5em 0;
  height: 80px;
  flex-shrink: 0;
  bottom: 0;
}

#footer-images ul {
  padding: 1em 0;
}

#footer-images li {
  list-style: none;
  margin: 0 10px;
  display: block;
  font-size: 16px;
}

#footer-images ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 90px 0 90px;
}

@media (max-width: 991.98px) {
  #footer-images ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
  }
}

.contain-2 {
  padding-top: 20px;
  padding-bottom: 20px;
}

contain-2 .heading {
  font-size: 40px;
  color: #042164;
}

.wrapper-flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-right: 1rem;
  margin-left: 3rem;
}

.containers {
  overflow: hidden;
  box-shadow: 0px 2px 8px 0px var(--clr-gray-light);
  background-color: white;
  text-align: center;
  border-radius: 1rem;
  position: relative;
  width: 280px;
  margin-bottom: 1rem;
}

.profile-img {
  width: 6rem;
  margin-top: 2.5rem;
}

.name {
  font-weight: bold;
  font-size: 1.5rem;
}

.description {
  margin: 1rem 2rem;
  font-size: 0.9rem;
}

.btn {
  border: none;
  border-radius: 30px;
  font-size: 1rem;
  color: white;
  width: 30%;
  padding: 0.5rem;
  background-color: #232b76;
}

.btn:hover {
  background-color: #9fdaf9;
}